import Button from '../Ui/Button'
import {useEffect, useRef,useState} from 'react';
import classes from './InputForm.module.css'
const Name=(props)=>{
   const nameChange= useRef();
   const[ error,setError]= useState(false);
   
   
  const submithandler=(event)=>{
    event.preventDefault();
    const userName=nameChange.current.value;
    if(userName.trim().length===0){
        setError(true);
        return;
    }
    
    
     props.onconfirm(userName);
   }
  
   const blurChangeHandler=()=>{
    if(nameChange.current.value.trim().length===0){
        setError(true);
        return;
    }
    
   }
    
return (
    <div className={classes.fullform}>
        <h2>What is Your Plan Today?</h2>
        <form onSubmit={submithandler} 
        className={`${classes.forms} ${error===true? classes.invalid:''}`}>
        
        <label htmlFor='text'>Name:</label>
        <input onBlur={blurChangeHandler} ref={nameChange} type='text' id='text'/>
        
        <div>
            <Button>Continue</Button>
        </div>
        
        </form>
        {error&& <p className={classes.invalidname} >Please Enter Name</p>}
    </div>
);
}

export default Name;