import Header from "./components/Header";
import UserList from "./components/UserList";
import Name from "./components/Name";
import {useState,useEffect} from 'react'

let isinitial=true;
function App() {
  const[userData,setUserData]=useState([ {id:Math.random(),username:'',background:0,name:''}]);
  
  const[names,setName]=useState();
  const[displayList,setDisplayList]=useState(false);
  const[backgroundcolor,setbackgroundcolor]=useState(0);
  const[error,setError]=useState();

   const nameZ=userData.map(x=>x.name);
  console.log('trick',nameZ); 
   useEffect(()=>{
     if(isinitial){
      isinitial=false;
       return;
       
     }
    const sendingData=async()=>{
      const response=await 
      fetch('https://todo-list-53e7f-default-rtdb.firebaseio.com/todo.json',{
        method:'PUT',
        body:JSON.stringify(userData)
      });
    }
    console.log('sending Data');
    sendingData();
   },[nameZ]);
   
   
    

   useEffect(()=>{
    const fetchingUserData= async()=>{
      const response=await 
      fetch('https://todo-list-53e7f-default-rtdb.firebaseio.com/todo.json');

      if(!response.ok){
        throw new Error ('sending data failed');
      }
    const responseData= await response.json();
    console.log(responseData);
    const loadedData=[];
    for (let key in responseData){
      loadedData.push({id:key,username:responseData[key].username,name:responseData[key].name,background:responseData[key].background})
    }
    console.log(loadedData);
    setUserData(loadedData);
    
    }
    fetchingUserData().catch((error)=>{
      setError(error.message);
    })
   },[]); 
    
   
  const userListHandler=(data)=>{
    if(backgroundcolor===2) 
    setbackgroundcolor(0);
    
    
    if(backgroundcolor<2){
      setbackgroundcolor(backgroundcolor+1);
    }
    setUserData((prevdata)=>{
      return[...prevdata,
        {id:Math.random(),username:names,background:backgroundcolor,name:data}];
    });
    
setDisplayList(true);
    
  }
  


  const removeItemHandler=(id)=>{
   
   const userList= userData.filter((item)=>item.id!==id);
   setUserData(userList);
   

  }
  
 
  
  
  const addsubmithandler=(userName)=>{



setName(userName);


  }
  
  const Data=userData.filter((item)=>item.username===names);
  
 
  return (
    <div>
     {names&& <Header  onAddData={userListHandler}/>}
     {!names && <Name onconfirm={addsubmithandler}></Name>}
     {names && Data.map((item)=>
     <UserList 
    
    removeData={removeItemHandler}
     color={item.background} 
     key={item.id}
     id={item.id} 
     userdata={item.name} 
     />)}
    </div>
  );
}

export default App;
