import classes from './UserList.module.css'
import Button from '../Ui/Button';
import Card from '../Ui/Card';
import {useState} from 'react'
const UserList =(props)=>{
 const[status,setStatus]=useState(false)
    let specialColor=''
     if(props.color===0){
        specialColor= classes.colortrue;
     }
     else if(props.color===1){
        specialColor= classes.coloryeellow;   
      }
      else {
        specialColor= classes.colorfalse;
      }
const removeHandler=()=>{
    props.removeData(props.id);
}

const completionHandler=()=>{
  setStatus(true);
}

  
return(
    <Card className={`${classes.list} ${specialColor} ${status&& classes.completion}`}>
        <div>
  <h4>{props.userdata}</h4>
  </div>
  <div>
  {!status &&<Button  onClick={completionHandler}>Done</Button>}
  <Button  onClick={removeHandler} className={classes.button}>X</Button>
  </div>
    </Card>
);
}
export default UserList;